import React from 'react';

import { useRouter } from 'next/router';

import { useQuery } from "@apollo/react-hooks";

import Query from '../components/query';
import GET_SECTION from '../apollo/queries/sections/section';

export default props => {

    const router = useRouter();

    const { data, loading, error } = useQuery(GET_SECTION, {
        variables: { sectionId: parseInt(5) }
    });

    if (loading) return <div>Loading...</div>;
    if (error) return <p>ERROR: {error.message}</p>;
    if (!data) return <p>Not found</p>;
    if (data) console.log(data.section);

    return (
        <>
            {data.section.length > 0 &&
                <h3>Testing new Page: {data.section.title}</h3>
            }
            <h1>{router.query.section}</h1>
            {/* <Query query={SECTION_QUERY(router.query.section)}>
                {({ data: { section } }) => {
                    return <SectionPage data={section} />
                }}
            </Query> */}
            <p>This is the blog post content.</p>
        </>
    );
}

const SectionPage = props => {
    return (
        <h3>Testing New Page: {props.title}</h3>
    );
}